import {render, RenderPosition} from '../framework/render.js';
import TripInfoView from '../view/trip-info-view.js';
import TripInfoMainView from '../view/trip-info-main-view.js';
import TripInfoCostView from '../view/trip-info-cost-view.js';
import TripFilterView from '../view/trip-filter-view.js';
import { createFilters } from '../mock/filter.js';
import { upperCaseFirst } from '../utils/utils.js';
import { FilterType, filters } from '../utils/filters.js';

const CSSClasses = {
  TRIP_MAIN_CONTAINER: '.trip-main',
  TRIP_FILTER_CONTAINER: '.trip-controls__filters',
};
export default class TripHeaderPresenter {
  #tripMainContainer = null;
  #tripInfoContainer = null;
  #tripFilterContainer = null;
  #filtersData = null; // 
  #previousFilterType = FilterType.EVERYTHING; //    (- - EVERYTHING)
  /** @type {import("./trip-content-presenter.js").default}    */
  #contentPresenter = null;

  constructor() {
    this.#tripMainContainer = document.querySelector(CSSClasses.TRIP_MAIN_CONTAINER); //       
    this.#tripInfoContainer = new TripInfoView(); //      ,    
    this.#tripFilterContainer = document.querySelector(CSSClasses.TRIP_FILTER_CONTAINER); //   
  }

  init(contentPresenter) {
    this.#contentPresenter = contentPresenter;
    this.#filtersData = {
      filters: createFilters(this.#contentPresenter.points),
      filtersClickCallback: this.#filtersClickHandler.bind(this),
    };

    render(this.#tripInfoContainer, this.#tripMainContainer, RenderPosition.AFTERBEGIN); //       
    render(new TripInfoMainView(), this.#tripInfoContainer.element); //      
    render(new TripInfoCostView(), this.#tripInfoContainer.element); //    
    render(new TripFilterView(this.#filtersData), this.#tripFilterContainer); //   
  }

  #filtersClickHandler(filterType) {
    const filterName = upperCaseFirst(filterType);

    //        
    if (this.#previousFilterType === filterName) {
      return;
    }

    const filteredPoints = filters[filterName](this.#contentPresenter.points);

    this.#previousFilterType = filterType;
    this.#contentPresenter.reRenderEventPoints(filteredPoints);
  }
}
